*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins" , sans-serif;
}

/* 
:root {
    --bgcolor:#f1f5fd
} */

html {
    font-size: 62.5%;
}
a {
    text-decoration: none;
    color: aliceblue;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    color: #333333;

}


/* navbar css  */

/* .navbar-bg{
    color: #333333;
    padding: 2rem 0;
    position: relative;
    z-index: 99;


} */

.navbar-brand{
    font-size: 2.0rem;
}

.navbar li {
    /* padding: 1rem 0; */
    font-size: 1.5rem;
  
}


.navbar li a {
    padding: 2rem 0;
    font: 1.9rem;
    font-weight: lighter;
    position: relative; 
}

.navbar li a:hover{
  color: red !important ;
  /* text-shadow: 0 0.2rem 0.3rem paleturquoise;   */
}

.btn-style{
   width: 12rem;
   height: 4rem;
   border-radius: 2rem;
   color: aliceblue;
   background-color: rgba(210, 105, 30, 0.665); 
   text-transform: capitalize;
   margin-right: 1rem;
   font-size: 1.6rem;
   margin-left: 1rem;


}




.btn-style:hover{
    border: 1px solid gray;
    background-color: #e9bfbf69;
    color: rgb(6, 6, 5);
    box-shadow: 1px red;
}


header{
    background-color: yallow;
    min-height: 100vh;
    display: flex;
}



.main-hero-container{
    height: 80vh;
    display: flex ;
    /* background-color: #fff; */
}



.main-hero-container h1 {
    text-transform: capitalize;
    font-weight: bold;

    /* color: red; */

}























@media (min-width : 1500px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container.xl,
    .container.xxl {
        max-width: 1500px !important;
    }
    

    
}



@media (min-width : 1400px) {
    .container,
    .container-lg,
    .container-md,
    .container-sm,
    .container.xl,
    .container.xxl {
        max-width: 1400px !important;
    }
    

    
}



